import Modal from '../../../../admin-columns/src/js/modules/modal'

export default class Export {

	constructor( element ) {
		this.element = element;
		this.Table = new Table( element.querySelector( '.ac-table' ) );
		this.Modal = new ExportModal( document.querySelector( '#ac-modal-export-php' ) );

		this.getExportJsonButton().setAttribute( 'disabled', true );
		this.getExportPhpButton().setAttribute( 'disabled', true );
		this.initEvents();
	}

	doAjaxExportCall() {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'get',
			data : {
				action : 'acp-export-php',
				encoder : 'php-export',
				list_screen_id : this.Table.getSelectedListScreens(),
				response_type : 'string',
				_ajax_nonce : this.element.querySelector( '[name="_ajax_nonce"]' ).value
			}
		} );
	}

	getExportPhpButton() {
		return this.element.querySelector( '[data-export="php"]' );
	}

	getExportJsonButton() {
		return this.element.querySelector( '[data-export="json"]' );
	}

	initEvents() {
		this.getExportPhpButton().addEventListener( 'click', e => {
			e.preventDefault();
			this.Modal.open();
			this.Modal.setCode( '' );

			this.doAjaxExportCall().done( response => {
				this.Modal.setCode( response );
			} );
		} );

		this.Table.element.querySelectorAll( 'input' ).forEach( el => {
			el.addEventListener( 'change', () => {
				this.getExportPhpButton().setAttribute( 'disabled', true );
				this.getExportJsonButton().setAttribute( 'disabled', true );

				if ( this.Table.getSelectedListScreens().length > 0 ) {
					this.getExportPhpButton().removeAttribute( 'disabled' );
					this.getExportJsonButton().removeAttribute( 'disabled' );
				}
			} );
		} );
	}

}

class Table {
	constructor( element ) {
		this.element = element;
	}

	getSelectedListScreens() {
		let ids = [];
		this.element.querySelectorAll( '[name="list_screen_id[]"]:checked' ).forEach( el => {
			ids.push( el.value );
		} );

		return ids;
	}

}

class ExportModal extends Modal {

	constructor( props ) {
		super( props );
		this.initCopyToClipboard();
	}

	getTextArea() {
		return this.el.querySelector( 'textarea' );
	}

	setCode( code ) {
		this.getTextArea().value = code;
	}

	initCopyToClipboard() {
		const textarea = this.getTextArea();
		textarea.addEventListener( 'click', () => {
			textarea.focus();
			textarea.select();
		} );
	}

}